import { Component } from "react";
import ItemOne from "./partials/itemOne";
import ItemTwo from "./partials/itemTwo";
import ItemThree from "./partials/itemThree";
import { useEffect } from 'react';
import { css } from "emotion";
import { Title, Button } from "reactism/elements";

// redux
import { connect } from 'react-redux'
import { getNews, getEventNews } from 'reactism/actions/news'

const Index = ({ getNews, getEventNews, news, eventNews }) => {

  useEffect(() => {
    getNews(),
    getEventNews()
  }, [])

  const baseStyle = css`
  background-color: #f6f6f6;
  display: grid;
  justify-items: center;
  div.title {
    padding: 1rem 0 0 0;
    justify-self: center;
    margin-left: -5.5rem;
  }

  padding-bottom: 2rem;
  box-shadow: border-box;
`;

  return (
    <div className={baseStyle}>
      <div className="title">
        <Title left={5}>Galleri</Title>
      </div>
      <ItemOne dataImage={ news } />
      <ItemTwo dataImage={ eventNews } />
      {/* <ItemThree dataImage={ media } /> */}
      {/* <Button color="success">MUAT LAINNYA</Button> */}
    </div>
  );
}


const mapStateToProps = state => {
  return { 
    news: state.news.news,
    eventNews: state.news.eventNews 
  }
};

export default connect(mapStateToProps, {
  getNews, getEventNews
})(Index);