import { css } from "emotion";
import { Card, Wrapper } from "reactism/elements";
import { LoadingEventNews } from 'reactism/components'
import { Link } from "Routes";
import { Color } from "reactism/utils";

const Item = ({ eventNews = [] }) => {
  const baseStyle = css`
    .wrapper {
      display: grid;
      grid-template-columns: 1fr 2fr;
      grid-column-gap: 1.5em;
      grid-template-areas: "col2 col1";
      > div:nth-child(1) {
        grid-area: col1;
        a {
          text-decoration: none;
          color: #000;
          transition: 0.2s ease;
          &:hover {
            color: ${Color.success};
          }
        }
        h2 {
          margin: 0;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
        p.content {
          display: -webkit-box;
          -webkit-line-clamp: 5;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
      }
      .imageBox {
        grid-area: col2;
        overflow: hidden;
        img {
          width: 100%;
          height: 33vh;
          object-fit: fill;
        }
      }
      span {
        p {
          margin-top: 0;
          color: rgba(0, 0, 0, 0.5);
        }
      }
      @media (max-width: 700px) {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr;
        grid-template-areas:
          "col2"
          "col1";
      }
    }
  `;
  return (
    <div className={baseStyle}>
      <Wrapper pl={7} pr={7}>
        {
          eventNews.length > 0
          ?
          eventNews.map(itemEvent => (
          <Card shadow="true" pd={18} mb={2}>
            <div className="wrapper">
              <div>
                <span>
                  <p>{itemEvent.created_at}</p>
                </span>
                <Link route="detailEvent" params={{ id: itemEvent.id }}>
                  <a>
                    <h2>{itemEvent.title}</h2>
                  </a>
                </Link>
                <p className="content">{itemEvent.desc}</p>
              </div>

              <div className="imageBox">
                <img src={itemEvent.image.image_url} />
              </div>
            </div>
          </Card>
        ))
        :
        <>
          <Card shadow="true" pd={18} mb={2}>
            <LoadingEventNews />
          </Card>
          <Card shadow="true" pd={18} mb={2}>
            <LoadingEventNews />
          </Card>
        </>
      }
      </Wrapper>
    </div>
  );
};

export default Item;
