import { Component } from "react";
import Complaint from "./partials/complaint";
import { Color } from "reactism/utils";
import { Wrapper } from "reactism/elements";
import Link from "next/link";
import { css } from "emotion";
import FormFeature from "./partials/formFeature";

class Index extends Component {
  render() {
    const baseStyle = css`
      background-color: #f6f6f6;
      padding-bottom: 1em;
      .wrapper {
        display: grid;
        grid-template-columns: repeat(3, 13%);
        justify-content: center;
        padding: 2rem 0 0.5rem 0;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        box-sizing: border-box;
        div {
          justify-self: center;
        }
        a {
          text-decoration: none;
          display: inline-block;
          padding: 6px;
          border-radius: 5px;
          color: #000;
          transition: 0.2s;
          &:hover {
            background-color: rgba(0, 153, 51, 0.15);
            color: ${Color.success};
          }
        }
        .active {
          background-color: rgba(0, 153, 51, 0.15);
          color: ${Color.success};
        }

        @media (max-width: 700px) {
          grid-template-columns: repeat(3, 25%);
        }
      }
    `;

    return (
      <div className={baseStyle}>
        <Wrapper pl={7} pr={7}>
          <div className="wrapper">
            <div>
              <Link href="/">
                <a className="active">Pengaduan</a>
              </Link>
            </div>
            <div>
              <Link href="/">
                <a>Tilang</a>
              </Link>
            </div>
            <div>
              <Link href="/">
                <a>Lelang</a>
              </Link>
            </div>
          </div>
          <Complaint />
          <FormFeature />
        </Wrapper>
      </div>
    );
  }
}

export default Index;
