import { cx, css } from "emotion";

const Title = ({ children, style, left }) => {
  const baseStyle = css`
    position: relative;
    h1 {
      font-size: 24px !important;
    }
    hr {
      border: none;
      border-bottom: 1.5px solid rgba(0, 0, 0, 0.1);
      width: 5rem;
      position: absolute;
      top: 0.4rem;
      left: ${left}rem;
    }

    @media (max-width: 700px) {
      hr {
        right: -10px;
      }
    }
  `;

  return (
    <div className={cx(baseStyle, style)} left={left}>
      <h1>{children}</h1>
      <hr />
    </div>
  );
};

export default Title;
