import { useState, useEffect } from "react";
import { css } from "emotion";
import ItemLastNews from "./partials/itemLastNews";
import { Title } from "reactism/elements";

// redux
import { connect } from 'react-redux'
import { getLatestNews } from 'reactism/actions/news'

  const LatestNews = ({ getLatestNews, latestNews }) => {

    useEffect(() => {
      getLatestNews()
    }, [])

    const baseStyle = css`
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: auto auto;
      background-color: #f6f6f6;
      div.title {
        padding: 5rem 0 1.5rem 0;
        justify-self: center;
        margin-left: -5.5rem;
      }
      div.itemLastNews {
        padding-bottom: 7rem;
      }

      @media (max-width: 700px) {
        div.title {
          padding-top: 2rem;
        }
        div.itemLastNews {
          padding-bottom: 4rem;
        }
      }
    `;

    return (
      <div className={baseStyle}>
        <div className="title">
          <Title left={10.5}>Berita Terbaru</Title>
        </div>
        <div className="itemLastNews">
          <ItemLastNews  latestNews={ latestNews } />
        </div>
      </div>
    );
  }
  

  const mapStateToProps = state => {
    return { latestNews: state.news.latestNews };
  };
  
  export default connect(mapStateToProps, {
    getLatestNews
  })(LatestNews);