import { css } from "emotion";
import { Card, Wrapper } from "reactism/elements";
import { Link } from "Routes";
import { Color } from "reactism/utils";
import Pagination from "react-js-pagination";
import { LoadingNews } from "reactism/components";

const Item = ({ news = [] }) => {

  const baseStyle = css`
    .wrapper {
      display: grid;
      grid-template-columns: 2fr 1fr;
      grid-column-gap: 1.5em;
      grid-template-areas: "col1 col2";
      > div:nth-child(1) {
        grid-area: col1;
        a {
          text-decoration: none;
          color: #000;
          transition: 0.2s ease;
          &:hover {
            color: ${Color.success};
          }
        }
        h2 {
          margin: 0;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
        p.content {
          display: -webkit-box;
          -webkit-line-clamp: 5;
          -webkit-box-orient: vertical;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
      .imageBox {
        position: relative;
        grid-area: col2;
        overflow: hidden;
        img {
          width: 100%;
          height: 30vh;
          object-fit: fill;
        }
        span.badgeCategory {
          padding: 5px 25px;
          background-color: rgba(0,153,51,0.8);
          border-radius: 2px;
          color: #fff;
          position: absolute;
          top: 15px;
          right: 15px;
        }
      }
      span {
        p {
          color: rgba(0, 0, 0, 0.5);
        }
      }
    }

    @media (max-width: 700px) {
      .wrapper {
          grid-template-rows: 1fr 1fr;
          grid-template-columns: 1fr;
          grid-template-areas:
            "col2"
            "col1";
      }
    }
  `;

  return (
    <div className={baseStyle}>
      <Wrapper>
        {
          news.length > 0
          ?
          news.map((itemNews, index) => {
              return(
                <Card shadow="true" pd={18} mb={2} key={index}>
                    <div className="wrapper">
                      <div>
                        <Link route="detailNews" params={{ id: itemNews.id }}>
                          <a>
                            <h2>{ itemNews.title }</h2>
                          </a>
                        </Link>
                        <p className="content">{ itemNews.desc }</p>
                        <span>
                          <p>{ itemNews.created_at }</p>
                        </span>
                      </div>
                      <div className="imageBox">
                        <img src={ itemNews.image.image_url } />
                        <span className="badgeCategory">
                          { itemNews.category.name || `Uncategories` }
                        </span>
                      </div>
                    </div>
                </Card>
              )
          })
          :
          <>
            <Card shadow="true" pd={18} mb={2}>
              <LoadingNews />
            </Card>
            <Card shadow="true" pd={18} mb={2}>
              <LoadingNews />
            </Card>
          </>
        }
      </Wrapper>
    </div>
  );
};

export default Item