import { css } from "emotion";
import { Card, Wrapper, Title } from "reactism/elements";

const VisionAndMission = () => {
  const baseStyle = css`
    background-color: #f6f6f6;
    display: grid;
    justify-items: center;
    div.title {
      padding: 2rem 0 1.5rem 0;
      justify-self: center;
      margin-left: -5.5rem;
    }
    .wrapper {
      display: grid;
      grid-template-columns: 1fr 2fr;
      grid-column-gap: 2.5em;
      div:nth-child(1) {
        box-sizing: border-box;
        position: relative;
        h1 {
          font-size: 3rem;
        }
        .icon {
          position: absolute;
          top: 10px;
          right: -50px;
          img {
            width: 55%;
          }
        }
        h1 {
          color: #fff;
          margin: 0;
          padding-left: 6.5rem;
        }
      }
      div:nth-child(2) {
        > div:nth-child(1) {
          border-bottom: 1px solid rgba(0, 0, 0, 0.1);
          margin-bottom: 1.3rem;
        }
      }

      @media (max-width: 700px) {
        grid-template-columns: 1fr;
        .icon {
          right: 0 !important;
        }
      }
    }
    padding-bottom: 2rem;
    box-shadow: border-box;
  `;

  return (
    <div className={baseStyle}>
      <div className="title">
        <Title left={9}>Visi dan Misi</Title>
      </div>
      <Wrapper pl={7} pr={7}>
        <Card shadow="true" pd={20}>
          <div className="wrapper">
            <div>
              <div>
                <Card color="success" mb={1} pd={25}>
                  <div className="icon">
                    <img src="/static/icon/ic_visi.svg" alt="icon visi" />
                  </div>
                  <h1>Visi</h1>
                </Card>
              </div>
              <div>
                <Card color="warning" pd={25}>
                  <div className="icon">
                    <img src="/static/icon/ic_misi.svg" alt="icon visi" />
                  </div>
                  <h1>Misi</h1>
                </Card>
              </div>
            </div>
            <div>
              <div>
                <p>
                  Mewujudkan Kejaksaan sebagai lembaga penegak hukum yang
                  melaksanakan tugasnya secara independen dengan menjunjung
                  tinggi HAM dalam negara hukum berdasarkan Pancasila
                </p>
              </div>
              <div>
                <ol>
                  <li>
                    <p>
                      Menyatukan tata pikir, tata laku dan tata kerja dalam
                      penegakan hokum
                    </p>
                  </li>
                  <li>
                    <p>
                      Optimalisasi pemberantasan KKN dan penuntasan pelanggaran
                      HAM
                    </p>
                  </li>
                  <li>
                    <p>
                      Menyesuaikan sistem dan tata laksana pelayanan dan
                      penegakan hukum dengan mengingat norma keagamaan,
                      kesesuliaan, kesopanan dengan memperhatikan rasa keadilan
                      dan nilai-nilai kemanusiaan dalam masyarakat.
                    </p>
                  </li>
                </ol>
              </div>
            </div>
          </div>
        </Card>
      </Wrapper>
    </div>
  );
};

export default VisionAndMission;
