import { css } from "emotion";
import Carousel from "nuka-carousel";
import { Color } from "reactism/utils";
import { LoadingEventNews } from 'reactism/components'
// assets

const Item = ({ eventNews = [] }) => {
  const wrapper = css`
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 4em;
    grid-template-rows: 30vh;
    > div {
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    div:nth-child(2) {
      p.date {
        span {
          color: ${Color.success};
        }
        color: rgba(0, 0, 0, 0.5);
      }

      h2 {
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
      p {
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
    }

    @media screen and (min-width: 768px) and (max-width:1024px) {
      grid-template-rows: 20vh;
    }


    @media (max-width: 700px) {
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr;
      grid-gap: 0;
    }
  `;

  return (
    <Carousel
      autoplay={ true }
      speed={ 200 }
      pauseOnHover={ true }
      renderCenterLeftControls={({ previousSlide }) => (
        <button onClick={previousSlide}>&#8249;</button>
      )}
      renderCenterRightControls={({ nextSlide }) => (
        <button onClick={nextSlide}>&#8250;</button>
      )}
    >
     {
        eventNews.length > 0 
        ?
        eventNews.map((events, index) => {
          return(
            <div className={wrapper} key={ index }>
              <div>
                <img src={ events.image.image_url } />
              </div>
              <div>
                <p className="date">
                  <span>Kegiatan : </span> { events.created_at }
                </p>

                <h2>{ events.title }</h2>
                <p>{ events.desc }</p>
              </div>
            </div>
          )
        })
        :
        <LoadingEventNews />
      }
    </Carousel>
  );
};

export default Item;
