import { Component } from "react";
import { useEffect } from 'react';
import { css } from "emotion";
import { Title, Button } from "reactism/elements";
import DetailNews from './detailNews';

// redux
import { connect } from 'react-redux'
import { getNews } from 'reactism/actions/news'

const Index = ({ getNews, news }) => {

  useEffect(() => {
    getNews()
  }, [])

  const baseStyle = css`
  background-color: #f6f6f6;
  display: grid;
  justify-items: center;
  div.title {
    padding: 1rem 0 1rem 0;
    justify-self: center;
    margin-left: -5.5rem;
  }

  padding-bottom: 2rem;
  box-shadow: border-box;
`;

  return (
    <div className={baseStyle}>
      <div className="title">
        <Title left={5}>Galleri</Title>
      </div>
      <DetailNews dataImage={ news } />
      <Button color="success">MUAT LAINNYA</Button>
    </div>
  );
}


const mapStateToProps = state => {
  return { 
    news: state.news.news,
    eventNews: state.news.eventNews 
  }
};

export default connect(mapStateToProps, {
  getNews
})(Index);