import PropTypes from "prop-types";
import { cx, css } from "emotion";
import { Background } from "reactism/utils";

const Card = ({
  children,
  style,
  transparent,
  shadow,
  color,
  pd,
  mb,
  mt,
  ...props
}) => {
  const baseStyle = css`
    ${transparent ? "background-color: transparent;" : Background(color)}
    box-shadow: ${
      shadow == "true" ? "0px 4px 10px rgba(0, 0, 0, 0.2);" : "none"
    };
    border-radius: 8px;
    box-sizing: border-box;
    padding: ${pd}px;
    margin-bottom: ${mb}rem;
    margin-top: ${mt}rem;
  `;

  return (
    <div className={cx(baseStyle, style)} {...props}>
      {children}
    </div>
  );
};

Card.propTypes = {
  color: PropTypes.string
};

Card.defaultProps = {
  color: "white"
};
export default Card;
