import React, { Component } from "react";
import { cx, css } from "emotion";
import { Title } from "reactism/elements";
import { Color } from "reactism/utils";

// Assets
import people from "reactism/static/people.jpg";
class WelcomeTwo extends Component {
  render() {
    const baseStyle = css`
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: 90vh;
      div:nth-child(1) {
        background-repeat: no-repeat;
        background-size: cover;
      }

      div:nth-child(2) {
        padding: 0 6rem;
        display: grid;
        grid-template-columns: 1fr;
        justify-content: center;
        align-content: center;
        background-color: #f6f6f6;
        div {
          h1 {
            font-size: 2.5rem;
            margin-bottom: -1rem;
          }
        }
        p {
          font-size: 1rem;
        }
        .position {
          color: ${Color.success};
        }
      }

      /* IPAD */

      @media screen and (min-width: 768px) and (max-width: 1024px) {
        grid-template-rows: 45vh;
        div:nth-child(2) {
          padding: 0 2rem;
          p {
            font-size: 0.8em;
          }
        }
      }

      /* MOBILE */
      @media (max-width: 700px) {
        grid-template-columns: 1fr;
        grid-template-rows: 70vh 60vh;
        grid-template-areas:
          "w"
          "p";

        div:nth-child(1) {
          grid-area: p;
        }

        div:nth-child(2) {
          grid-area: w;
          padding: 0 1rem;
        }
      }
    `;
    return (
      <>
        <div className={baseStyle}>
          <div
            className={css`
              background-image: url("${people}");
            `}
          />

          <div>
            <div>
              <Title left={7.4}>Sambutan</Title>
              <h1>Paian Tumanggor</h1>
              <p className="position">Kepala Kejaksaan Negeri</p>
              <p>
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Sunt
                labore ratione dignissimos? Unde corporis dolore beatae tempora
                autem, amet aperiam aspernatur fugiat rem neque libero ipsam non
                porro, tenetur sunt. Lorem ipsum, dolor sit amet consectetur
                adipisicing elit. Explicabo ipsam consectetur quaerat, odio in
                possimus accusantium, ad provident commodi fugit, saepe
                voluptatum nihil. Rem ullam, reiciendis incidunt cumque optio
                beatae!
              </p>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default WelcomeTwo;
