import { css } from "emotion";
import { Wrapper } from "reactism/elements";

const ItemThree = ({ dataImage }) => {
  const baseStyle = css`
    .wrapper {
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
      padding-bottom: 2em;
      display: grid;
      grid-template-columns: 1fr;
      > div:nth-child(1) {
        width: 60%;
        h2 {
          margin-bottom: 0;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
        p {
          margin-top: 5px;
        }
      }
      .imgGallery {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 1.5em;
        grid-template-areas:
          "col1 col1 col2 col3"
          "col1 col1 col4 col5"
          "col6 col7 col8 col9"
          "col10 col11 col12 col13";
        div {
          overflow: hidden;
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
          box-shadow: 0px 4px 0px 0 rgba(0, 0, 0, 0.2);
          border-radius: 8px;
        }
        div:nth-child(1) {
          height: 55vh;
          grid-area: col1;
        }
        div:nth-child(2) {
          grid-area: col2;
        }
        div:nth-child(3) {
          grid-area: col3;
        }
        div:nth-child(4) {
          grid-area: col4;
        }
        div:nth-child(5) {
          grid-area: col5;
        }
        div:nth-child(6) {
          grid-area: col6;
        }
        div:nth-child(7) {
          grid-area: col7;
        }
        div:nth-child(8) {
          grid-area: col8;
        }
        div:nth-child(9) {
          grid-area: col9;
        }
        div:nth-child(10) {
          grid-area: col10;
        }
        div:nth-child(11) {
          grid-area: col11;
        }
        div:nth-child(12) {
          grid-area: col12;
        }
        div:nth-child(13) {
          grid-area: col13;
        }
        div:nth-child(2),
        div:nth-child(3),
        div:nth-child(4),
        div:nth-child(5),
        div:nth-child(6),
        div:nth-child(7),
        div:nth-child(8),
        div:nth-child(9),
        div:nth-child(10),
        div:nth-child(11),
        div:nth-child(12),
        div:nth-child(13) {
          height: 25.7vh;
        }
      }
    }
    padding-bottom: 2em;

    @media (max-width: 700px) {
      .wrapper {
        .imgGallery {
          div:nth-child(1) {
            height: 30vh;
          }
          grid-template-columns: repeat(2, 1fr);
          grid-template-areas:
            "col1 col1"
            "col2 col3"
            "col4 col4"
            "col5 col6"
            "col7 col7"
            "col8 col9"
            "col10 col10"
            "col11 col12"
            "col13 col13";
        }
      }
    }
  `;

  return (
    <div className={baseStyle}>
      <Wrapper pl={7} pr={7}>
        <div className="wrapper">
          <div>
            <h2>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna
            </h2>
            <p>20 Oktober 2018</p>
          </div>
          <div className="imgGallery">
            {dataImage.map((itemImg, i) => (
              <div key={i}>
                <img src={itemImg.image} alt="" />
              </div>
            ))}
          </div>
        </div>
      </Wrapper>
    </div>
  );
};

export default ItemThree;
