import { css, keyframes } from 'emotion'

const baseStyle = css`
  display: grid;
  padding-bottom: 3em;
  padding-top: 2em;
  grid-template-columns: 1fr;
  grid-row-gap: 1em;
  div {
    border-radius: 5px;
    animation: animate;
  }
  > div:nth-of-type(1){
    background-color: #eaeaea;
    height: 5vh;
    width: 50%;
  }
  > div:nth-of-type(2){
    background-color: #eaeaea;
    height: 2vh;
    width: 20%;
  }
  .loadItem {
    display: grid;
    width: 175vh;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1.5em;
    grid-template-areas:
      "col1 col1 col2 col4"
      "col1 col1 col3 col5";
    div {
      background-color: #eaeaea;
    }
    div:nth-of-type(1) {
      height: 55vh;
      grid-area: col1;
    }
    div:nth-of-type(2) {
      grid-area: col2;
    }
    div:nth-of-type(3) {
      grid-area: col3;
    }
    div:nth-of-type(4) {
      grid-area: col4;
    }
    div:nth-of-type(5) {
      grid-area: col5;
    }
    div:nth-of-type(2),
    div:nth-of-type(3),
    div:nth-of-type(4),
    div:nth-of-type(5) {
      height: 25.7vh;
    }
  }

  @media screen and (min-width: 768px) and (max-width: 1024px) {
    .loadItem {
      width: 60vh;
      div:nth-of-type(1) {
        height: 30vh;
      }
      grid-template-columns: repeat(2, 1fr);
      grid-template-areas:
        "col1 col1"
        "col2 col3"
        "col4 col5";
    }
  }

  @media(max-width: 700px) {
    .loadItem {
      width: 49vh;
      div:nth-of-type(1) {
        height: 30vh;
      }
      grid-template-columns: repeat(2, 1fr);
      grid-template-areas:
        "col1 col1"
        "col2 col3"
        "col4 col5";
    }
  }
`;

const animate = keyframes`
  0% {
    opacity: 0%;
  }

  50% {
    opacity: 30%;
  }

  70% {
    opacity: 60%
  }

  100%{
    opacity: 100%;
  }
`;

const LoadingGallery = () => {
  return(
    <div className={baseStyle}>
      <div />
      <div />
      <div className="loadItem">
        <div />
        <div />
        <div />
        <div />
        <div />
      </div>
    </div>
  )
}

export default LoadingGallery