import { css, keyframes } from 'emotion'

const LoadingDetailNewsGallery = () => {

  const baseStyle = css`
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1.5em;
    padding-bottom: 3em;
    > div {
      animation: animate;
      width: 250px;
      height: 200px;
      background-color: #eaeaea;
      border-radius: 5px;
    }
    @media(max-width: 700px) {
      grid-template-columns: repeat(2, 1fr);
      > div {
        width: 155px;
      }
    }
  `

const animate = keyframes`
  0% {
    opacity: 0%;
  }

  50% {
    opacity: 30%;
  }

  70% {
    opacity: 60%
  }

  100%{
    opacity: 100%;
  }
`;

  return(
    <div className={ baseStyle }>
      <div/>
      <div/>
      <div/>
      <div/>
      
      <div/>
      <div/>
      <div/>
      <div/>
    </div>
  )
}

export default LoadingDetailNewsGallery