import { css } from "emotion";
import { Card, Button } from "reactism/elements";

const FormFeature = () => {
  const baseStyle = css`
    margin-top: 2em;
    h2 {
      margin: 0;
    }
    table {
      margin-top: 1em;
      width: 100%;
      box-sizing: border-box;
      tr {
        td {
          padding-bottom: 1em;
          vertical-align: middle;
          p {
            float: left;
          }
          span {
            float: left;
            margin-top: 17px;
            padding-left: 5px;
            color: red;
          }
          input,
          textarea {
            outline: none;
            box-shadow: none;
            border-radius: 5px;
            border: 1.5px solid rgba(0, 0, 0, 0.5);
            padding: 10px;
            font-size: 1em;
            box-sizing: border-box;
            width: 100%;
            transition: 0.2s ease;
            &:focus {
              border-color: rgba(0, 153, 51, 1);
              box-shadow: 0px 0px 10px 0px rgba(0, 153, 51, 0.5);
            }
          }
        }
      }
      td:nth-child(1) {
        width: 32%;
      }
    }

    @media (max-width: 700px) {
      table {
        td {
          input,
          textarea {
            padding: 6px !important;
          }
        }
        td:nth-child(1) {
          width: 45%;
          font-size: 0.7em;
        }
      }
    }
  `;

  return (
    <div className={baseStyle}>
      <Card shadow="true" pd={30} mb={2}>
        <div>
          <h2>Pelapor</h2>
          <table>
            <tr>
              <td>
                <p>Nama Pelapor</p>
                <span>*</span>
              </td>
              <td>
                <input type="text" />
              </td>
            </tr>
            <tr>
              <td>
                <p>Nama Pelapor</p>
                <span>*</span>
              </td>
              <td>
                <input type="text" />
              </td>
            </tr>
            <tr>
              <td>
                <p>Alamat Pelapor</p>
                <span>*</span>
              </td>
              <td>
                <input type="text" />
              </td>
            </tr>
            <tr>
              <td>
                <p>Email Pelapor</p>
                <span>*</span>
              </td>
              <td>
                <input type="text" />
              </td>
            </tr>
            <tr>
              <td>
                <p>Nomor Telp. Pelapor</p>
                <span>*</span>
              </td>
              <td>
                <input type="text" />
              </td>
            </tr>
          </table>
        </div>

        <div>
          <h2>Terlapor</h2>
          <table>
            <tr>
              <td>
                <p>Nama Terlapor</p>
                <span>*</span>
              </td>
              <td>
                <input type="text" />
              </td>
            </tr>
            <tr>
              <td>
                <p>Jabatan</p>
                <span>*</span>
              </td>
              <td>
                <input type="text" />
              </td>
            </tr>
            <tr>
              <td>
                <p>Satuan Kerja</p>
                <span>*</span>
              </td>
              <td>
                <input type="text" />
              </td>
            </tr>
          </table>
        </div>

        <div>
          <h2>Terlapor</h2>
          <table>
            <tr>
              <td>
                <p>Subjek Pengaduan</p>
                <span>*</span>
              </td>
              <td>
                <textarea rows="3" type="text" />
              </td>
            </tr>
            <tr>
              <td>
                <p>Isi Pengaduan</p>
                <span>*</span>
              </td>
              <td>
                <input type="text" />
              </td>
            </tr>
            <Button color="success">KIRIM PENGADUAN</Button>
          </table>
        </div>
      </Card>
    </div>
  );
};

export default FormFeature;
