import { css } from "emotion";

const Item = ({ data }) => {
  const wrapper = css`
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-column-gap: 2.5rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding-bottom: 1rem;
    margin-bottom: 1rem;
    &:last-child {
      margin-bottom: 0;
    }
    div:nth-child(1) {
      > div {
        height: 34vh;
        background-size: cover;
        background-repeat: no-repeat;
        background-position-y: -1.5rem;
      }
    }

    div:nth-child(2) {
      > div:nth-child(1) {
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        padding-bottom: 1.3rem;
      }
      h1 {
        margin: 0;
        font-size: 2rem;
      }
      p {
        margin: 0;
      }
      table {
        tr {
          td {
            padding-right: 1.5rem;
            p {
              color: rgba(0, 0, 0, 0.5);
            }
          }
        }
      }
    }

    @media (max-width: 700px) {
      grid-column-gap: 1em;
      padding-bottom: 0;
      div:nth-child(1) {
        > div {
          height: 23vh;
        }
      }
      div:nth-child(2) {
        > div:nth-child(1) {
          padding-bottom: 0.6rem;
        }
        h1 {
          font-size: 1em;
        }
        p,
        ul {
          font-size: 0.7em;
        }
        div:nth-child(2) {
          h3 {
            font-size: 0.7em;
          }
          table {
            font-size: 0.7em;
          }
        }
      }
    }
  `;

  return (
    <>
      {data.map(item => (
        <div className={wrapper}>
          <div>
            <div
              className={css`
                background-image: url("${item.image}");
              `}
            />
          </div>
          <div>
            <div>
              <h1>{item.name}</h1>
              <p>{item.position}</p>
            </div>
            <div>
              <h3>{item.subPosition}</h3>
              <div>
                <table>
                  <tr>
                    <td>
                      <p>NIP</p>
                    </td>
                    <td>{item.nip}</td>
                  </tr>
                  <tr>
                    <td>
                      <p>Telp</p>
                    </td>
                    <td>{item.telp}</td>
                  </tr>
                  <tr>
                    <td>
                      <p>Email</p>
                    </td>
                    <td>{item.email}</td>
                  </tr>
                </table>
              </div>
            </div>
          </div>
        </div>
      ))}
    </>
  );
};

export default Item;
