import { css, keyframes } from "emotion";

const baseStyle = css`
  div {
    background-color: #eaeaea;
    width: 85%;
    animation: animate;
  }
  div:nth-of-type(1) {
    height: 27vh;
    margin-bottom: 1.5em;
  }
  div:nth-of-type(2),
  div:nth-of-type(3) {
    height: 20px;
    border: none;
    margin-bottom: 10px;
  }
  div:nth-of-type(4),
  div:nth-of-type(5),
  div:nth-of-type(6),
  div:nth-of-type(7),
  div:nth-of-type(8),
  div:nth-of-type(9) {
    height: 10px;
    border: none;
    margin-bottom: 10px;
    &:nth-last-of-type() {
      margin-bottom: 0;
    }
  }
  div:nth-of-type(4) {
    margin-top: 1.5em;
  }
  div:nth-of-type(9) {
    margin-top: 1.5em;
    width: 30%;
  }

  /* ipad */

  @media screen and (min-width: 768px) and (max-width:1024px) {
    grid-column-gap: 1em;
    div {
      padding: 0;
    }
    div:nth-of-type(1) {
      height: 12vh;
      margin-bottom: 0;
    }
  }

  /* mobile */
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    div {
      width: 100%;
    }
  }
`;

const animate = keyframes`
  0% {
    opacity: 0%;
  }

  50% {
    opacity: 30%;
  }

  100%{
    opacity: 100%;
  }
`;

const LoadingLatestNews = () => {
  return (
    <>
    <div className={baseStyle}>
      <div />
      <div />
      <div />
      <div />
      <div />
      <div />
      <div />
      <div />
      <div />
    </div>
    <div className={baseStyle}>
      <div />
      <div />
      <div />
      <div />
      <div />
      <div />
      <div />
      <div />
      <div />
    </div>
  <div className={baseStyle}>
    <div />
    <div />
    <div />
    <div />
    <div />
    <div />
    <div />
    <div />
    <div />
  </div>
  </>
  );
};

export default LoadingLatestNews;
