import { useEffect } from "react";
import Item from "./partials/item";
import { css } from "emotion";
import { Title, Button } from "reactism/elements";

// redux
import { connect } from 'react-redux'
import { getEventNews, loadMoreEvent } from 'reactism/actions/news'

const Index = ({ getEventNews, eventNews, loadMoreEvent, currentPage }) => {

  useEffect(() => {
    getEventNews()
  }, [])

  const loadMore = () => {
    loadMoreEvent(currentPage)
  }

    const baseStyle = css`
      background-color: #f6f6f6;
      display: grid;
      justify-items: center;
      div.title {
        padding: 1rem 0 1.5rem 0;
        justify-self: center;
        margin-left: -5.5rem;
      }

      padding-bottom: 2rem;
      box-shadow: border-box;
    `;

    return (
      <div className={baseStyle}>
        <div className="title">
          <Title left={7}>Kegiatan</Title>
        </div>
        <Item eventNews={ eventNews } />
        <Button color="success" onClick={ loadMore }>MUAT LAINNYA</Button>
      </div>
    );
  }

  const mapStateToProps = state => {
    return { 
      eventNews: state.news.eventNews,
      currentPage: state.news.currentPage 
    };
  };
  
  export default connect(mapStateToProps, {
    getEventNews, loadMoreEvent
  })(Index);
  
