import { css } from "emotion";
import { Card, Wrapper } from "reactism/elements";

const BuildingAndLobby = () => {
  const wrapper = css`
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5em;
    grid-template-areas:
      "col1 col2 col4"
      "col1 col3 col4";
    div:nth-child(1),
    div:nth-child(2),
    div:nth-child(3) {
      background-size: cover;
      background-repeat: no-repeat;
    }
    div:nth-child(1) {
      grid-area: col1;
    }
    div:nth-child(2) {
      grid-area: col2;
    }
    div:nth-child(3) {
      grid-area: col3;
    }
    div:nth-child(4) {
      grid-area: col4;
    }

    @media (max-width: 700px) {
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 25vh 25vh;
      grid-template-areas:
        "col4 col4"
        "col1 col1"
        "col2 col3";
    }
  `;

  return (
    <>
      <Wrapper pl={7} pr={7}>
        <Card shadow="true" pd={20} mb={2}>
          <div className={wrapper}>
            <div
              className={css`
                background-image: url("/static/build.jpg");
              `}
            />
            <div
              className={css`
                background-image: url("/static/lobby.jpg");
              `}
            />
            <div
              className={css`
                background-image: url("/static/lobby2.jpg");
              `}
            />
            <div>
              <h2>Gedung dan Lobby</h2>
              <p>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
                eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut
                enim ad minim veniam, quis nostrud exercitation ullamco laboris
                nisi ut aliquip ex ea commodo consequat.Lorem ipsum dolor sit
                amet, consectetur adipiscing elit, sed do eiusmod tempor
                incididunt ut labore et dolore magna aliqua. Ut enim ad minim
                veniam, quis nostrud exercitation ullamco laboris nisi ut
                aliquip ex ea commodo consequat. incididunt ut labore et dolore
                magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation
                ullamco laboris nisi ut aliquip ex ea commodo consequat.
              </p>
            </div>
          </div>
        </Card>
      </Wrapper>
    </>
  );
};

export default BuildingAndLobby;
