import { useState, useEffect } from 'react';
import Item from "./partials/item";
import { css } from "emotion";
import { Title, Button } from "reactism/elements";

// redux
import { connect } from 'react-redux'
import { getNews, loadMoreNews } from 'reactism/actions/news'

const Index = ({ getNews, currentPage, news, loadMoreNews }) => {
  useEffect(() => {
    getNews();
  }, [])

  // const [isLoading, setIsLoading] = useState(false);

  const loadMore = () => {
    loadMoreNews(currentPage)
  }

  
  const baseStyle = css`
      background-color: #f6f6f6;
      display: grid;
      justify-items: center;
      div.title {
        padding: 1rem 0 1.5rem 0;
        justify-self: center;
        margin-left: -5.5rem;
      }

      padding-bottom: 2rem;
      box-shadow: border-box;
    `;

    return (
      <div className={baseStyle}>
        <div className="title">
          <Title left={5}>Berita</Title>
        </div>
          <Item news={ news } />
        <Button color="success" onClick={ loadMore }>MUAT LAINNYA</Button>
      </div>
    );
}

const mapStateToProps = state => {
  return { 
    news: state.news.news ,
    currentPage : state.news.currentPage
  };
};

export default connect(mapStateToProps, {
  getNews, loadMoreNews
})(Index);
