import { css, keyframes } from 'emotion'

const baseStyle = css`
  display: grid;
  grid-template-columns: 55vh 110vh;
  grid-column-gap: 1.5em;
  grid-template-areas: "col1 col2";
  div {
    animation: animate;
  }
  > div:nth-of-type(1) {
    > div {
      height: 30vh;
      background-color: #eaeaea;
    }
  }

  > div:nth-of-type(2) {
    display: grid;
    grid-template-rows: 12px 25px 1fr 1fr 1fr 1fr 1fr;
    grid-row-gap: 1em;
    > div {
      background-color: #eaeaea;
    }
    div:nth-of-type(1){
      width: 20%;
    }
    div:nth-of-type(2){
      width: 90%;
    }
    grid-area: col2;
  }

  @media(max-width: 700px) {
    grid-template-columns: 1fr;
    grid-row-gap: 1em;
    grid-template-areas: "col1" "col2";
    div {
      width: 48vh;
    }
    > div:nth-of-type(2) {
      grid-row-gap: 1em;
    }
  }
`

const animate = keyframes`
  0% {
    opacity: 0%;
  }

  50% {
    opacity: 30%;
  }

  70% {
    opacity: 60%
  }

  100%{
    opacity: 100%;
  }

`;

const LoadingEventNews = () => {
  return(
    <div className={baseStyle}>
      <div>
        <div></div>
      </div>

      <div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
    </div>
  )
}

export default LoadingEventNews