import PropTypes from "prop-types";
import React, { Component } from "react";
import { cx, css } from "emotion";

const Wrapper = ({ children, style, pr, pl, ...props }) => {
  const baseStyle = css`
    max-width: 1190px;
    padding-right: 16px;
    padding-left: 16px;
    margin: auto;
    box-sizing: border-box;

    @media (max-width: 700px) {
      padding-right: 1.5%;
      padding-left: 1.5%;
    }
  `;
  return (
    <div className={cx(baseStyle, style)} pr={pr} pl={pl} {...props}>
      {children}
    </div>
  );
};

export default Wrapper;
