import { css } from "emotion";
import { useState, useEffect } from 'react';
import { Wrapper } from "reactism/elements";
import { Color } from 'reactism/utils'
import LightBox from "./ligthbox";
import { LoadingDetailNewsGallery } from 'reactism/components';

const DetailNewsGallery = ({ dataImage = [] }) => {

  const baseStyle = css`
    .lightbox {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0,0,0,0.8);
      z-index: 99;
      visibility: collapse;
      opacity: 0;
      display: grid;
      img {
        transition: .4s;
        z-index: 999;
        width: 20%;
        margin: auto;
        opacity: 0;
        /* transition: .2s; */
      }
      &.show {
        visibility: visible;
        opacity: 1;
        img {
          opacity: 1;
          width: 50%;
          border-radius: 5px;
        }
      }
    }
    .wrapper {
      /* border-bottom: 1px solid rgba(0, 0, 0, 0.1); */
      padding-bottom: 2em;
      display: grid;
      grid-template-columns: 1fr;
      a {
        text-decoration: none;
        color: #000;
        &:hover {
          color: ${ Color.success };
        }
      }
      .imgGallery {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 1.5em;
        div {
          cursor: zoom-in;
          overflow: hidden;
          height: 200px;
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
          box-shadow: 0px 4px 0px 0 rgba(0, 0, 0, 0.2);
          border-radius: 8px;
        }
      }
    }

    @media (max-width: 700px) {
      .wrapper {
        .imgGallery {
          div:nth-child(1) {
            height: 30vh;
          }
          grid-template-columns: repeat(2, 1fr);
        }
      }
    }
  `;

  const [ onVisible, setOnVisible ] = useState(false);

  const handleClick = () => {
    // let lightBox = document.getElementById('lightbox');
    // lightBox.classList.add('show');
    setOnVisible(!onVisible);
  };

  return (
    <div className={baseStyle}>
      <LightBox onVisible={ onVisible } setOnVisible={ setOnVisible } />
      <Wrapper>
      {
        dataImage.length > 0 
        ?
          <div className="wrapper">
            <div className="imgGallery">
                {
                  dataImage.map((itemImg, i) => (
                  <div key={i} onClick={ handleClick } params={{ id: itemImg.id }}>
                    <img id="imgUrl" src={itemImg.image.thumbnail} />
                  </div>
                ))
                }
            </div>
          </div>
        :
        <LoadingDetailNewsGallery />
      }
      </Wrapper>
    </div>
  );
};

export default DetailNewsGallery;
