import React, { Component } from "react";
import { cx, css } from "emotion";
import { Link } from 'Routes'
import { Button } from "reactism/elements";

// Assets
import hammer from "reactism/static/hammer.jpg";

class WelcomeOne extends Component {
  render() {
    const baseStyle = css`
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: 90vh;
      div:nth-child(1) {
        padding: 0 3rem;
        display: grid;
        grid-template-columns: 1fr;
        background-color: #fff;
        button {
          width: 35%;
        }
        justify-content: center;
        align-content: center;
        div {
          h1 {
            font-size: 2.5rem;
            margin-bottom: 0;
          }
        }
        p {
          font-size: 1rem;
        }
      }
      div:nth-child(2) {
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: -10rem;
      }

      @media screen and (min-width: 768px) and (max-width: 1024px) {
        grid-template-rows: 40vh;
        
        div:nth-of-type(1) {
          padding: 0 1.5rem;
          button {
            width: 60%;
          }

          div {
            h1 {
              font-size: 1.5rem;
              }
          }
        }
      }

      /* MOBILE */
      @media (max-width: 700px) {
        grid-template-columns: 1fr;
        grid-template-rows: 80vh 60vh;
        div:nth-of-type(1) {
          padding: 0 0.5rem;
          button {
            width: 60%;
          }
        }
      }
    `;
    return (
      <>
        <div className={baseStyle}>
          <div>
            <div>
              <h1>Selamat Datang di Kejaksaan Negeri Barru</h1>
              <p>
                Lorem ipsum, dolor sit amet consectetur adipisicing elit.
                Explicabo ipsam consectetur quaerat, odio in possimus
                accusantium, ad provident commodi fugit, saepe voluptatum nihil.
                Rem ullam, reiciendis incidunt cumque optio beatae!
              </p>
              <Link route="profil">
                <Button color="success">PROFIL LENGKAP</Button>
              </Link>
            </div>
          </div>
          <div
            className={css`
              background-image: url("${hammer}");
            `}
          />
        </div>
      </>
    );
  }
}

export default WelcomeOne;
