import { css } from "emotion";
import { Card } from "reactism/elements";
import { Color } from "reactism/utils";

const Intelligence = () => {
  const wrapper = css`
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-column-gap: 2em;
    > div:nth-child(1) {
      div {
        height: 35vh;
        background-repeat: no-repeat;
        background-size: cover;
      }
      h2 {
        margin-bottom: 0;
      }
      p {
        margin-top: 0;
        color: ${Color.success};
      }
    }
    div:nth-child(2) {
      > div:nth-child(1) {
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
      }
      li {
        font-family: "Muli", sans-serif;
      }
      h2 {
        margin: 0;
      }
    }

    @media (max-width: 700px) {
      grid-template-columns: 1fr;
      grid-template-rows: 50vh 1fr;
    }
  `;

  return (
    <Card shadow="true" pd={20} mb={2} mt={2}>
      <div className={wrapper}>
        <div>
          <div
            className={css`
              background-image: url("/static/people.jpg");
            `}
          />
          <h2>Ari Harianto</h2>
          <p>Kasubag Pembinaan</p>
        </div>
        <div>
          <div>
            <h2>Intellijen</h2>
            <p>
              Sub Bagian Pembinaan Kejaksaan Negeri Barru mempunyai tugas
              melakukan pembinaan atas manajemen dan pembangunan prasarana dan
              prasarana, pengelolaan ketatausahaan, kepegawaian, kesejahteraan
              pegawai, keuangan, perlengkapan organisasi dan tata laksana
              pengelelolaan teknis atas milik negara yang menjadi tanggung
              jawabnya serta pemberian dukungan pelayanan teknis dan
              administrasi bagi seluruh satuan kerja dilingkungan Kejaksaan
              Negeri Barru dalam rangka memperlancar pelaksanaan tugas.
            </p>
          </div>

          <div>
            <h3>
              Dalam melaksanakan tugas, Sub bagian Pembinaan menyelenggarakan
              fungsi
            </h3>
            <ol>
              <li>
                <p>
                  Melakukan koordinasi, integrasi dan sinkronisasi serta membina
                  kerjasama seluruh satuan kerja di lingkungan Kejaksaan Negeri
                  di bidang administrasi
                </p>
              </li>
              <li>
                <p>
                  Melakukan pembinaan organisasi dan tata laksana urusan
                  ketatausahaan dan mengelola keuangan, kepegawaian,
                  perlengkapan dan milik negara yang menjadi tanggung jawabnya
                </p>
              </li>
              <li>
                <p>
                  Melakukan pembinaan dan peningkatan kemampuan, keterampilan
                  dan integritas kepribadian aparat Kejaksaan di daerah
                  hukumnya.
                </p>
              </li>
            </ol>

            <h3>
              Sub bagian Pembinaan terdiri dari:Sub bagian Pembinaan terdiri
              dari
            </h3>
            <ol>
              <li>
                <p>
                  Urusan Kepegawaian yang mempunyai tugas melakukan urusan
                  kepegawaian, peningkatan integritas dan kepribadian serta
                  kesejahteraan pegawai.
                </p>
              </li>
              <li>
                <p>
                  Urusan Keuangan yang mempunyai tugas melakukan urusan
                  keuangan.
                </p>
              </li>
              <li>
                <p>
                  Urusan Perlengkapan yang mempunyai tugas melakukan urusan
                  perlengkapan dan kerumahtanggaan.Urusan Perlengkapan yang
                  mempunyai tugas melakukan urusan perlengkapan dan
                  kerumahtanggaan.
                </p>
              </li>
            </ol>
          </div>
        </div>
      </div>
    </Card>
  );
};

export default Intelligence;
