
import { NEWS, NEWS_DETAIL, EVENT_NEWS, LATEST_NEWS, EVENT_PAGINATION, EVENT_SET_PAGE} from 'reactism/types'
import { HOST, API_NEWS } from 'reactism/config/api';
import { Router } from 'Routes'
import { NEWS_PAGINATION, NEWS_SET_PAGE } from '../types';

export const getNews = () => dispatch => {
  fetch(HOST + API_NEWS + `?category=berita`, {
    method: 'GET',
    headers: {
      'key-company': 'a9527695-ba83-4e53-8509-2747723c2c8b'
    }
  })
  .then(res => res.json())
  .then(data => {
    dispatch({ type: NEWS, response: data.response })
  })
  .catch(err => console.log(err))
};

// loadMore 

export const loadMoreNews = page => dispatch => {
  fetch(HOST + API_NEWS + `?category=berita&page=${ page }`, {
    method: 'GET',
    headers: {
      'key-company': 'a9527695-ba83-4e53-8509-2747723c2c8b'
    }
  })
  .then(res => res.json())
  .then(data => {
    dispatch({ type: NEWS_PAGINATION, response: data.response })
    dispatch({ type: NEWS_SET_PAGE })
  })
  .catch(err => console.log(err))
};

