// Detail News

export const getDetailNews = id => dispatch => {
  return new Promise(resolve => {
    fetch(`${ HOST + API_NEWS }/read/${ id }`, {
      method: 'GET',
      headers: {
        'key-company': 'a9527695-ba83-4e53-8509-2747723c2c8b'
      }
    })
    .then(response => {
      return response.json()
    })
    .then(data => {
      dispatch({ type: NEWS_DETAIL, response: data.response })
      resolve(data.response)
    })
    .catch(error => {
      // console.log(error)
      Router.back()
    })
  })
}