import { css } from "emotion";
import { Card } from "reactism/elements";

const Complaint = () => {
  const wrapper = css`
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-column-gap: 2em;
    > div:nth-child(1) {
      .wrapIcon {
        border: 1px solid rgba(255, 255, 255, 0.3);
        border-radius: 5px;
        /* height: 40vh; */
        img {
          width: 50%;
        }
        h1 {
          margin-bottom: 0;
          margin-top: 5px;
        }
        padding: 3em 1em;
        text-align: center;
      }
    }

    div:nth-child(2) {
      li {
        font-family: "Muli", sans-serif;
      }
    }

    @media (max-width: 700px) {
      grid-template-columns: 1fr;
      grid-template-rows: 33vh 1fr;
      grid-row-gap: 1em;
      p {
        font-size: 0.85em;
      }
      > div:nth-of-type(1) {
        .wrapIcon {
          img {
            width: 35%;
          }
        }
      }
    }
  `;

  return (
    <Card shadow="true" mt={2} pd={20} color="success">
      <div className={wrapper}>
        <div>
          <div className="wrapIcon">
            <img src="/static/icon/pengaduan.svg" alt="icon pengaduan" />
            <h1>Pengaduan</h1>
          </div>
        </div>
        <div>
          <p>
            Penerimaan pengaduan masyarakat / Whistle Blowing System (WBS)
            melalui website ini adalah untuk pengaduan yang berkaitan dengan
            dugaan pelanggaran perilaku ataupun ketidakprofesionalan Jaksa dan
            Pegawai dalam melaksanakan tugas.Kejaksaan RI tidak akan
            menindaklanjuti:
          </p>
          <ol>
            <li>
              <p>Pengaduan yang tidak jelas.</p>
            </li>
            <li>
              <p>
                Pengaduan mengenai dugaan terjadinya suatu tindak pidana. Perlu
                diinformasikan bahwa Kejaksaan RI hanya memiliki wewenang untuk
                melakukan penyidikan terhadap tindak pidana tertentu yang diatur
                secara khusus oleh undang-undang, di antaranya adalah tindak
                pidana korupsi, perikanan, dan pelanggaran HAM berat yang
                penyidikannya sudah mendapat persetujuan DPR Pengaduan atau
                informasi mengenai dugaan terjadinya tindak pidana tertentu
                tersebut dapat Anda sampaikan kepada Kejaksaan Tinggi maupun
                Kejaksaan Negeri di wilayah.
              </p>
            </li>
          </ol>
        </div>
      </div>
    </Card>
  );
};

export default Complaint;
