import { css, keyframes } from 'emotion'

const LoadingDetail = () => {

  const baseStyle = css`
    background: #fff;
    width: 175vh;
    border-radius: 8px;
    display: grid;
    padding: 20px;
    box-sizing: border-box;
    grid-template-columns: 1fr;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
    grid-gap: 1em;
    .headerLoad {
      div{
        animation: animate;
      }
      display: grid;
      grid-template-columns: 50% 10%;
      grid-column-gap: 27.5em;
      div {
        background: #eaeaea;
        height: 5vh;
        margin-bottom: 1em;
      }
    }

    .imgLoad {
      background: #eaeaea;
      height: 70vh;
    }

    .descLoad {
      background: #eaeaea;
      height: 20vh;
      width: 80%;
      justify-self: center;
    }

    @media(max-width: 768px){
      width: 53vh !important;
      .headerLoad {
        grid-template-columns: 1fr;
      }
      .imgLoad {
        width: 48vh;
        height: 30vh;
      }
      .descLoad {
      width: 90%;
      justify-self: center;
    }
    }
  `;

  const animate = keyframes`
      0% {
        opacity: 0%;
      }

      50% {
        opacity: 30%;
      }

      70% {
        opacity: 60%
      }

      100%{
        opacity: 100%;
      }
      
  `;

  return (
    <div className={baseStyle}>
      <div className="headerLoad">
        <div></div>
        <div></div>
      </div>
      <div className="imgLoad"></div>
      <div className="descLoad"></div>
    </div>
  )
}

export default LoadingDetail