import { css } from "emotion";
import { useEffect } from 'react'
import { Color } from 'reactism/utils'
import { Wrapper, Card } from "reactism/elements";
import { LoadingLatestNews } from "reactism/components";
import { Link } from "Routes";



const ItemLastNews = ({ latestNews = [] }) => {

  const wrapper = css`
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: center;
    div {
      padding: 0 1.5rem;
      span {
        color: rgba(0, 0, 0, 0.5);
        font-family: "Muli", sans-serif;
      }
      p.content {
        display: -webkit-box;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
      a {
        text-decoration: none;
        color: #000;
        &:hover {
          color: ${Color.success}
        }
        h2 {
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
      }
    }
    div:nth-child(1) {
      height: 30vh;
      background-repeat: no-repeat;
      background-size: cover;
    }
    div:nth-child(2) {
      border-right: 2px solid #f6f6f6;
      border-left: 2px solid #f6f6f6;
    }

    /* MOBILE */
    @media (max-width: 700px) {
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr;
      div {
        padding: 0;
      }
      div:nth-child(2) {
        border: none;
        border-top: 2px solid #f6f6f6;
        border-bottom: 2px solid #f6f6f6;
        padding-top: 1.5rem;
      }
      div:nth-child(3) {
        padding-top: 1.5rem;
      }
    }
  `;

  return (
    <Wrapper pl={7} pr={7}>
      <Card pd={20} shadow="true">
        <div className={wrapper}>
          {
            latestNews.length > 0 
            ?
            latestNews.splice(0, 3).map((latest, index) => {
              return(
                <div key={ index }>
                <div className={ css`background-image: url('${latest.image.image_url}');` } />
                  <Link route="detailnews" params={{ id: latest.id }}>
                    <a>
                      <h2 className="titleNews">{latest.title}</h2>
                    </a>
                  </Link>
                  <p className="content">{latest.desc}</p>
                  <span>{latest.created_at}</span>
                </div>
              )
            })
            :
            <LoadingLatestNews />
          }
        </div>
      </Card>
    </Wrapper>
  );
};


export default ItemLastNews