import { Component } from "react";
import BuildingAndLobby from "./partials/buildingAndLobby";
import PublicInformation from "./partials/publicInformation";
import Hall from "./partials/hall";
import Musholla from "./partials/musholla";
import Clinic from "./partials/Clinic";
import Library from "./partials/library";
import { css } from "emotion";
import { Title } from "reactism/elements";

class Index extends Component {
  render() {
    const baseStyle = css`
      background-color: #f6f6f6;
      display: grid;
      justify-items: center;
      div.title {
        padding: 2rem 0 1.5rem 0;
        justify-self: center;
        margin-left: -5.5rem;
      }
      padding-bottom: 2em;
    `;

    return (
      <div className={baseStyle}>
        <div className="title">
          <Title left={5.4}>Sarana</Title>
        </div>
        <BuildingAndLobby />
        <PublicInformation />
        <Hall />
        <Musholla />
        <Clinic />
        <Library />
      </div>
    );
  }
}

export default Index;
